//var  graceChecker = require("../../../common/graceChecker.js");
import graceChecker from '../../../common/graceChecker.js'
import common from '../../../common/common.js'
import util from '../../../common/util.js'

export default {
	data() {
		return {
			userInfo:[],		//用户信息
			words:'获取验证码',
			mobile:'',
			ismobile:false,
			issend:false,
			wait:60,
			utype:0,		//类型 0:零售 1:批发
			array:[],
			index:0,
		}
	},
	onLoad() {
		var that = this
		var userInfo = uni.getStorageSync('userInfo');
		
	},
	methods: {
		chooseUType(e){
			var that = this
			that.utype = e.detail.value
		},
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			var that = this
			var index = e.target.value
			var m_id = that.marketList[index].m_id
			that.m_id = m_id
			that.index = index
		},
		//检测手机号输入
		onkeyinput(e){
			var that = this
			var mobile = e.detail.value;
			if(util.isPoneAvailable(mobile)){
				that.ismobile = true;
				that.mobile = mobile;
			}else{
				that.ismobile = false;
				that.mobile = '';
			}
		},
		
		//获取验证码
		getcode(){
			var that = this
			var mobile = that.mobile
			if(!that.ismobile){
				if(that.wait == 60){
					uni.showToast({
						title: '请输入正确手机号',
						duration: 1500,
						icon :'none'
					});
				}
				return false;
			}
			that.ismobile = false;
			
			uni.showLoading();
			uni.request({
				url: common.sjApiUrl + 'merchant/getRegSafeCode',
				method: 'GET',
				data: {
					mobile:mobile
				},
				success: res => {
					uni.hideLoading();
					if(res.data.code > 0){							
						uni.showToast({
							title: res.data.msg,
							duration: 1500,
							icon :'none'
						});
						return false;
					}else{
						that.time();
						that.ismobile = false;
					}
				},
				fail: () => {},
				complete: () => {}
			});
			
		},
		
		time(){
			var that = this
			var wait = that.wait
			if (wait == 0) {  
				that.ismobile = true; 
				that.words = "获取验证码";  
				that.wait = 60;  
			} else {  
				that.ismobile = false; 
				that.words = "重新发送("+wait+")";			
				wait--;
				that.wait = wait
				setTimeout(function() {  
					that.time()  
				},  
				1000)  
			}  
		},
		
		//注册
		userlogin(){
			uni.redirectTo({
				url: '/pages/shop/userlogin/userlogin',
			});
		},
		formSubmit: function (e) {
			var that = this
			var data = e.detail.value;
			var mobile = data.mobile
			var password = data.password
			var repassword = data.repassword
			var safecode = data.safecode
			var utype = that.utype
							
			
			if(mobile == ''){					
				uni.showToast({
					title: '请输入手机号码',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			if(!util.isPoneAvailable(mobile)){					
				uni.showToast({
					title: '手机号格式不正确',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			if(safecode.length != 4){
				uni.showToast({
					title: '验证码错误',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			if(password == '' || password.length < 6){					
				uni.showToast({
					title: '密码至少6位',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			if(repassword == '' || repassword.length < 6){				
				uni.showToast({
					title: '确认密码至少6位',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			if(password != repassword){					
				uni.showToast({
					title: '两次密码输入不一致',
					duration: 1500,
					icon :'none'
				});
				return false
			}
			
			uni.showLoading({
				title: '注册中'
			});
			
			uni.request({
				url: common.sjApiUrl + 'merchant/userReg',
				method: 'GET',
				data: {
					utype:utype,
					mobile:mobile,
					password:password,
					repassword:repassword,
					safecode:safecode
				},
				success: res => {
					uni.hideLoading();
					var data = res.data
					console.log(data);
					if(data.code == 0){
						uni.setStorageSync('userInfo',data.shop);
						uni.showToast({
							title: '注册成功',
							duration: 1500,
							icon:'success',
							success() {
								uni.navigateTo({
									url:'/pages/shop/userlogin/userlogin'
								})
							}
						});
						
					}else{
						uni.showToast({
							title: data.msg,
							duration: 1500,
							icon:'none'
						});
						return false
					}
				},
				fail: (res) => {
					console.log(res)
				},
				complete: (res) => {
					console.log(res)
				}
			});
			
		},
		formReset: function (e) {
			console.log("清空数据")
			this.chosen = ''
		}
	}
}
